<template>
    <router-view></router-view>
    <meta name="viewport" content="width=device-width, initial-scale=1">
    <link rel="stylesheet" href="https://www.w3schools.com/w3css/4/w3.css">
    <img style=" top:-100px;; position: absolute; width: 130px;height:90px;" id="myAnimation" v-bind:src="require('@/assets/plane.png')"/>
    <img style=" top:0px; left:10px; position: absolute; width: 170px;height:120px;" v-bind:src="require('@/assets/logo.png')"/>
    <h1 class="hh1 w3-container w3-center w3-animate-top">Why don't you</h1>
    <h2 class=" hh2 w3-container w3-center w3-animate-top">Chill...?</h2>
    <body>
    </body>
    <button v-on:click="mymove" class="bb">Take me away!</button>
    <a class="ss1"  @click="tours">Tours</a>
    <a class="ss2" @click="about">About us</a>
    <div class="sticker-wrapper">
  <aside @click="deals" class="sticker yellow">
    <p>Special Deals</p>
    <span class="fold"></span>
  </aside>
</div>
</template>
<script>
function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}
var id=null
export default {
  name: 'Mainpage',
  components: {
  },
  methods: {
 
    mymove() {
    var elem = document.getElementById("myAnimation");   
    var pos = 350;
    clearInterval(id);
    id = setInterval(frame, 10);
    function frame() {
        if (pos == 0) {
        clearInterval(id);
        } else {
        pos=pos-3; 
        elem.style.top = pos + 'px'; 
        elem.style.right = pos + 'px'; 
        }
    }
    sleep(2000).then(() => { this.$router.push("takeme"); });
    },
    deals() {
        this.$router.push("/deals");
    },
    about() {
        this.$router.push("/about");
    },
    tours() {
      this.$router.push("/tours"); 
    }
  }
}

</script>

<style>
.sticker {
  position: relative;
  display: table;
  width: 10em;
  height: 10em;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 20px;
  left:140px;
  padding: 1em;
  border-radius: 100%;
  background: rgb(255,215,7);
  box-sizing: border-box;
  overflow: hidden;
}
.sticker p {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  color: black;
}
.sticker .fold {
  content: "";
  display: block;
  width: 10em;
  height: 10em;
  position: absolute;
  right: -6em;
  top: -6em;
  border-radius: 100%;
  background: -webkit-linear-gradient(-135deg, transparent 11.3em, #feeba7 10em, #feeba7 10em, #feeba7 85%);
}

.sticker.yellow {
  background: rgb(255,215,7);
  background: -webkit-linear-gradient(-135deg, transparent 2.82em, rgb(255,215,7) 0, rgb(255,215,7));
}
.ss1{
    position: absolute;
    font-size: 30px;
    top:40px;
    right:300px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;

}

.ss2{
    position: absolute;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    top:40px;
    font-size: 30px;
    right:140px;
} 

.ss1:hover {opacity: 0.2;}
.ss2:hover {opacity: 0.2;}
.bb {
  font-size: 60px;
  height:190px;
  width:400px;
  position: absolute;
  right:80px;
  top:250px;
  font-family: 'Montserrat';
  display: inline-block;
  padding: 15px 25px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #4caa04;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
}

.bb:hover {background-color: #3e898e}

.bb:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
.hh1{
    color:rgb(12, 100, 44);
    position: absolute;
    font-size: 60px;
    right:390px;
    font-family: 'Montserrat';
}
.hh2{
    color:rgb(12, 100, 44);
    font-family: 'Montserrat';
    position: absolute;
    right:130px;
    font-size: 120px;
}
body{
    background-image: url("@/assets/Capture.PNG");
    background-repeat: no-repeat;
    background-size: 100%;
}
</style>