<template>
  <div>
      <h1>{{ dest }}</h1>
      <ul>
        <li>{{ price }}$</li>
        <li>{{ number_people }}</li>
      </ul>
      <button @click="achange()">Add</button>
      <button @click="ochange()">Less</button>
  </div>
</template>
  
  <script>
    export default {
      props: {dest: String,
              price: Number,
              number_people: Number},
      methods:{
        achange(){
          this.$emit('achange')
        },
        ochange(){
          this.$emit('ochange')
        }
      }
    }
  </script>
  
  <style>

  </style>
  